      text="–ë–µ—Å–ø–ª–∞—Ç–Ω—ã–π –æ–Ω–ª–∞–π–Ω –∫–∞–ª—å–∫—É–ª—è—Ç–æ—Ä –ø–æ–º–æ–∂–µ—Ç —Ä–∞—Å—Å—á–∏—Ç–∞—Ç—å –æ—Ç–º–æ—Å—Ç–∫—É –≤–æ–∫—Ä—É–≥ –≤–∞—à–µ–≥–æ –¥–æ–º–∞. –° –µ–≥–æ –ø–æ–º–æ—â—å—é –≤—ã —É–∑–Ω–∞–µ—Ç–µ —Ç–æ—á–Ω–æ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –º–∞—Ç–µ—Ä–∏–∞–ª–æ–≤, –∫–æ—Ç–æ—Ä—ã–µ –ø–æ–Ω–∞–¥–æ–±—è—Ç—Å—è –¥–ª—è —Å—Ç—Ä–æ–∏—Ç–µ–ª—å—Å—Ç–≤–∞. –í—Å–µ —Ä–µ–∑—É–ª—å—Ç–∞—Ç—ã —Ä–∞—Å—á–µ—Ç–æ–≤ –º–æ–∂–Ω–æ —Å–∫–∞—á–∞—Ç—å –Ω–∞ –≤–∞—à –∫–æ–º–ø—å—é—Ç–µ—Ä –∏–ª–∏ —Å–º–∞—Ä—Ç—Ñ–æ–Ω. –ö–∞–ª—å–∫—É–ª—è—Ç–æ—Ä –æ—Ç–º–æ—Å—Ç–∫–∏ –≤—ã—á–∏—Å–ª–∏—Ç, –∫–∞–∫–æ–π –æ–±—ä–µ–º –º–∞—Ç–µ—Ä–∏–∞–ª–æ–≤ –ø–æ–Ω–∞–¥–æ–±–∏—Ç—Å—è –¥–ª—è —Å—Ç—Ä–æ–∏—Ç–µ–ª—å—Å—Ç–≤–∞ —Å–ª–æ–∂–Ω–æ–π –ø—Ä—è–º–æ—É–≥–æ–ª—å–Ω–æ–π –∫–æ–Ω—Å—Ç—Ä—É–∫—Ü–∏–∏, —É—á–∏—Ç—ã–≤–∞—è –≤–Ω—É—Ç—Ä–µ–Ω–Ω–∏–µ –∏ –≤–Ω–µ—à–Ω–∏–µ —É–≥–ª—ã –ø–æ –ø–µ—Ä–∏–º–µ—Ç—Ä—É. –¢–∞–∫–∂–µ –≤ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞—Ö –≤—ã—á–∏—Å–ª–µ–Ω–∏–π –≤—ã —É–≤–∏–¥–∏—Ç–µ, —Å–∫–æ–ª—å–∫–æ –ø–æ—Ç—Ä–µ–±—É–µ—Ç—Å—è –±—Ä—É—Å—á–∞—Ç–∫–∏, —É—Ç–µ–ø–ª–∏—Ç–µ–ª—è, –≥–∏–¥—Ä–æ–∏–∑–æ–ª—è—Ü–∏–æ–Ω–Ω–æ–≥–æ –º–∞—Ç–µ—Ä–∏–∞–ª–∞, –≥–µ–æ—Ç–µ–∫—Å—Ç–∏–ª—è, –æ–ø–∞–ª—É–±–∫–∏, –±–æ—Ä–¥—é—Ä–∞, –∞—Ä–º–∞—Ç—É—Ä–Ω–æ–π —Å–µ—Ç–∫–∏, —â–µ–±–Ω—è, –ø–µ—Å–∫–∞ –∏ –±–µ—Ç–æ–Ω–∞. –ü–æ–∫–∞–∑–∞—Ç–µ–ª–∏ —Ä–∞—Å—Å—á–∏—Ç—ã–≤–∞—é—Ç—Å—è, –∏—Å—Ö–æ–¥—è –∏–∑ –ø–µ—Ä–∏–º–µ—Ç—Ä–∞, –ø–ª–æ—â–∞–¥–∏ –∏ –æ–±—ä–µ–º–∞. –û–±—Ä–∞—Ç–∏—Ç–µ –≤–Ω–∏–º–∞–Ω–∏–µ –Ω–∞ —Ä–µ–∫–æ–º–µ–Ω–¥—É–µ–º—ã–µ —Ä–∞–∑–º–µ—Ä—ã –æ—Ç–º–æ—Å—Ç–∫–∏ - –µ–µ —à–∏—Ä–∏–Ω–∞ –¥–æ–ª–∂–Ω–∞ —Å–æ—Å—Ç–∞–≤–ª—è—Ç—å –æ—Ç 80 –¥–æ 120 —Å–º, –∞ –Ω–∞ —É—á–∞—Å—Ç–∫–∞—Ö —Å–æ —Å–ª–æ–∂–Ω—ã–º–∏ –≥–µ–æ–ª–æ–≥–∏—á–µ—Å–∫–∏–º–∏ —É—Å–ª–æ–≤–∏—è–º–∏, –∫–∞–∫ –∫–∞—Ä—Å—Ç—ã - –æ—Ç 150 –¥–æ 300 —Å–º."
    />
    <modal
      :modal="modal"
      @modalDisable="modalDisable"
      titlePage="–ö–ê–õ–¨–ö–£–õ–Ø–¢–û–† –û–¢–ú–û–°–¢–ö–ò"
    />
  </div>
</template>

<script>
import { defineComponent } from "vue";
import HelpBetter from "../newComponents/HelpBetter.vue";
import BlockSection from "../newComponents/BlockSection.vue";
import BlockSectionResult from "../newComponents/BlockSectionResult.vue";
import readme from "./readme.vue";
import FormQuetions from '../newComponents/FormQuetions.vue';
import QrCode from '../newComponents/QrCode.vue';
import modal from "./modal.vue";
import axios from 'axios'

export default defineComponent({
  data() {
    return {
      modal: false,
      width: 1,
      external: 4,
      internal: 0,

      paving: 0.06,
      concrete: 0.1,
      insulation: 0.05,
      rubble: 0.3,
      sand: 0.2,

      letters: [
        'A', 'B', 'C', 'D', 'F', 'G', 'H', 'J', 'K', 'L', 'M', 'N', 'P', 'Q', 'R', 'S', 'T', 'V', 'W', 'X', 'Y', 'Z'
      ],
      sides: [
        {
          title: '–î–ª–∏–Ω–∞ —Å—Ç–æ—Ä–æ–Ω—ã A:',
          value: ''
        },
        {
          title: '–î–ª–∏–Ω–∞ —Å—Ç–æ—Ä–æ–Ω—ã B:',
          value: ''
        },
        {
          title: '–î–ª–∏–Ω–∞ —Å—Ç–æ—Ä–æ–Ω—ã C:',
          value: ''
        },
        {
          title: '–î–ª–∏–Ω–∞ —Å—Ç–æ—Ä–æ–Ω—ã D:',
          value: ''
        },
      ],
      
      checKgeotextile: false,
      checKreinforcing: false,
      checKborder: false,
      checKformwork: false,
      checKwaterproofing: false
    };
  },
  methods: {
    addSide() {
      let letter = this.letters[this.sides.length]
      console.log(letter)
      let newElem = {
        title: `–î–ª–∏–Ω–∞ —Å—Ç–æ—Ä–æ–Ω—ã ${letter}:`,
        value: ''
      }
      this.sides.push(newElem)
    },
    modalDisable() {
      this.modal = false
    },
    modalActive() {
      this.modal = true
    },
    saveFile() {
      let state = {
        head1: '–æ–±—â–∏–π —Ä–∞–∑–º–µ—Ä –æ—Ç–º–æ—Å—Ç–∫–∏:',
        head2: '–û–±—â–µ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —É–≥–ª–æ–≤:',
        head3: '–ü–µ—Ä–∏–º–µ—Ç—Ä –¥–æ–º–∞:',
        head4: '–®–∏—Ä–∏–Ω–∞ –æ—Ç–º–æ—Å—Ç–∫–∏:',
        head5: '–í–Ω–µ—à–Ω–∏–µ —É–≥–ª—ã –ê:',
        head6: '–í–Ω—É—Ç—Ä–µ–Ω–Ω–∏–µ —É–≥–ª—ã –∞:',
        head7: '–æ–±—â–∏–π –æ–±—ä–µ–º –º–∞—Ç–µ—Ä–∏–∞–ª–æ–≤:',
        head8: '–û–±—ä–µ–º –±—Ä—É—Å—á–∞—Ç–∫–∏ –†:',
        head9: '–û–±—ä–µ–º –±–µ—Ç–æ–Ω–∞ –í:',
        head10: '–û–±—ä–µ–º —É—Ç–µ–ø–ª–∏—Ç–µ–ª—è U:',
        head11: '–û–±—ä–µ–º —â–µ–±–µ–Ω—è G:',
        head12: '–û–±—ä–µ–º –ø–µ—Å–∫–∞ S:',
        head13: '–ø–ª–æ—â–∞–¥—å –≥–µ–æ—Ç–µ–∫—Å—Ç–∏–ª—è:',
        head14: '–ø–ª–æ—â–∞–¥—å –∞—Ä–º.—Å–µ—Ç–∫–∏:',
        head15: '–¥–ª–∏–Ω–∞ –±–æ—Ä–¥—é—Ä–∞:',
        head16: '–¥–ª–∏–Ω–∞ –æ–ø–∞–ª—É–±–∫–∏:',
        head17: '–ø–ª–æ—â–∞–¥—å –≥–∏–¥—Ä–æ–∏–∑–æ–ª—è—Ü–∏–∏:',

        title1: this.result,
        title2: this.external + this.internal,
        title3: this.perimeter || 0,
        title4: this.width || 0,
        title5: this.external || 0,
        title6: this.internal || 0,
        title7: this.pcirsResult,
        title8: this.resultPaving,
        title9: this.resultConcrete,
        title10: this.resultInsulation,
        title11: this.resultRubble,
        title12: this.resultSand,
        title13: this.result,
        title14: this.result,
        title15: this.border,
        title16: this.perimeter,
        title17: this.waterproofing,
      };
      axios({
        method: 'post',
        url: '/sendmail2.php',
        headers: { 'content-type': 'application/json' },
        data: state
      }).then(res => {
        var link = document.createElement('a');
        link.setAttribute('href','/data.txt');
        link.setAttribute('download','download');
        link.click();
        return alert('–§–∞–π–ª —Å–æ—Ö—Ä–∞–Ω–µ–Ω');
      });
    },

  },
  computed: {
    perimeter() {
      let result1 = 0
      this.sides.forEach((element) => {
        result1 += element.value * this.width
      })
      return result1
    },
    result() {
      if (this.external < 4) {
        return 'null'
      } else {
        // return this.perimeter * this.width + (this.width * this.width * 2 * 2) - (this.external - 4) + this.internal
        let result1 = 0
        let result2 = (this.width * this.width) * this.external - (this.width * this.width) * this.internal
        this.sides.forEach((element) => {
          result1 += element.value * this.width
        })
        return result1 + result2
      }
    },
    resultPaving() {
      return (this.result * this.paving).toFixed(2)
    },
    resultConcrete() {
      return (this.result * this.concrete).toFixed(2)
    },
    resultInsulation() {
      return (this.result * this.insulation).toFixed(2)
    },
    resultRubble() {
      return (this.result * this.rubble).toFixed(2)
    },
    resultSand() {
      return (this.result * this.sand).toFixed(2)
    },
    pcirsResult() {
      return (Number(this.resultPaving) + Number(this.resultConcrete) + Number(this.resultInsulation) + Number(this.resultRubble) + Number(this.resultSand)).toFixed(2)
    },
    border() {
      return this.perimeter + (this.width * 8) + ((this.external - 4) * 2) + (this.internal* 2)
    },
    waterproofing() {
      return (this.perimeter / 100 * 73).toFixed(2)
    },
    formwork() {
      return ''
    }
  },
  components: {
    BlockSection,
    BlockSectionResult,
    HelpBetter,
    readme,
    FormQuetions,
    QrCode,
    modal
    // "readme": readme,
    // "modal": modal,
    // HelpBetter
  },
});
</script>

