      text="–ß—Ç–æ–±—ã –ø–æ—Å—Ç—Ä–æ–µ–Ω–Ω–æ–µ –∑–¥–∞–Ω–∏–µ –±—ã–ª–æ –Ω–∞–¥–µ–∂–Ω—ã–º, –¥–æ–ª–≥–æ–≤–µ—á–Ω—ã–º –∏ –±–µ–∑–æ–ø–∞—Å–Ω—ã–º, –≤–∞–∂–Ω–æ –ø—Ä–∞–≤–∏–ª—å–Ω–æ —Ä–∞—Å—Å—á–∏—Ç–∞—Ç—å –≥–ª—É–±–∏–Ω—É –∑–∞–ª–æ–∂–µ–Ω–∏—è —Ñ—É–Ω–¥–∞–º–µ–Ω—Ç–∞. –û–Ω–∞ –∑–∞–≤–∏—Å–∏—Ç –æ—Ç —Ä–∞–∑–Ω—ã—Ö —Ñ–∞–∫—Ç–æ—Ä–æ–≤, –≤ —Ç–æ–º —á–∏—Å–ª–µ –∫–ª–∏–º–∞—Ç–∞, –≥–µ–æ–ª–æ–≥–∏–∏ –∏ –≥–∏–¥—Ä–æ–ª–æ–≥–∏–∏ –º–µ—Å—Ç–Ω–æ—Å—Ç–∏, –∞ —Ç–∞–∫–∂–µ –æ—Ç –æ—Å–æ–±–µ–Ω–Ω–æ—Å—Ç–µ–π –≤–æ–∑–≤–æ–¥–∏–º–æ–≥–æ —Å—Ç—Ä–æ–µ–Ω–∏—è. –ë–µ—Å–ø–ª–∞—Ç–Ω—ã–π –æ–Ω–ª–∞–π–Ω –∫–∞–ª—å–∫—É–ª—è—Ç–æ—Ä –≥–ª—É–±–∏–Ω—ã —Ñ—É–Ω–¥–∞–º–µ–Ω—Ç–∞ –ø–æ–∑–≤–æ–ª—è–µ—Ç —Ä–∞—Å—Å—á–∏—Ç–∞—Ç—å, –Ω–∞ —Å–∫–æ–ª—å–∫–æ –º–µ—Ç—Ä–æ–≤ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –∑–∞–≥–ª—É–±–ª—è—Ç—å –æ—Å–Ω–æ–≤–∞–Ω–∏–µ —Å—Ç—Ä–æ–µ–Ω–∏—è, —á—Ç–æ–±—ã –æ–Ω–æ –≥–∞—Ä–∞–Ω—Ç–∏—Ä–æ–≤–∞–Ω–Ω–æ –≤—ã–¥–µ—Ä–∂–∞–ª–æ –Ω–µ —Ç–æ–ª—å–∫–æ —Å–∞–º–æ –∑–¥–∞–Ω–∏–µ, –Ω–æ –∏ –≤–æ–∑–¥–µ–π—Å—Ç–≤–∏–µ –æ–∫—Ä—É–∂–∞—é—â–µ–π —Å—Ä–µ–¥—ã. –ö–∞–ª—å–∫—É–ª—è—Ç–æ—Ä —Ä–∞–±–æ—Ç–∞–µ—Ç –ø–æ –∞–∫—Ç—É–∞–ª—å–Ω—ã–º —Å—Ç—Ä–æ–∏—Ç–µ–ª—å–Ω—ã–º –Ω–æ—Ä–º–∞–º –∏ –ø—Ä–∞–≤–∏–ª–∞–º, —Ç–∞–∫–∏–º –∫–∞–∫ ¬´–û—Å–Ω–æ–≤–∞–Ω–∏—è –∑–¥–∞–Ω–∏–π –∏ —Å–æ–æ—Ä—É–∂–µ–Ω–∏–π¬ª –∏ ¬´–°—Ç—Ä–æ–∏—Ç–µ–ª—å–Ω–∞—è –∫–ª–∏–º–∞—Ç–æ–ª–æ–≥–∏—è¬ª. –ü–æ—ç—Ç–æ–º—É —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞–º —Ä–∞—Å—á–µ—Ç–∞ –º–æ–∂–Ω–æ –¥–æ–≤–µ—Ä—è—Ç—å. –í –±–∞–∑–µ –∫–∞–ª—å–∫—É–ª—è—Ç–æ—Ä–∞ –ø—Ä–µ–¥—Å—Ç–∞–≤–ª–µ–Ω—ã –≤—Å–µ —Å—É–±—ä–µ–∫—Ç—ã –†–æ—Å—Å–∏–π—Å–∫–æ–π –§–µ–¥–µ—Ä–∞—Ü–∏–∏, –∞ —Ç–∞–∫–∂–µ –∫—Ä—É–ø–Ω—ã–µ –≥–æ—Ä–æ–¥–∞ –≤ —Ä–µ–≥–∏–æ–Ω–∞—Ö. –ò—Ç–æ–≥–∏ —Ä–∞—Å—á–µ—Ç–æ–≤ –∫–∞–ª—å–∫—É–ª—è—Ç–æ—Ä–∞ –≥–ª—É–±–∏–Ω—ã –∑–∞–≥–ª—É–±–ª–µ–Ω–∏—è –æ—Å–Ω–æ–≤–∞–Ω–∏—è –æ—Å–Ω–æ–≤—ã–≤–∞—é—Ç—Å—è –Ω–∞ –Ω–æ—Ä–º–∞—Ç–∏–≤–Ω–æ–º –∏ —Ä–∞—Å—á–µ—Ç–Ω–æ–º –ø–æ–∫–∞–∑–∞—Ç–µ–ª—è—Ö –≥–ª—É–±–∏–Ω—ã –ø—Ä–æ–º–µ—Ä–∑–∞–Ω–∏—è –≥—Ä—É–Ω—Ç–∞, —É—Ä–æ–≤–Ω—è –∑–∞–ª–µ–≥–∞–Ω–∏—è –≥—Ä—É–Ω—Ç–æ–≤—ã—Ö –≤–æ–¥ –∏ —Å—Ä–µ–¥–Ω–µ–π —Ç–µ–º–ø–µ—Ä–∞—Ç—É—Ä—ã –≤–æ–∑–¥—É—Ö–∞. –†–∞—Å—á–µ—Ç—ã –∞–∫—Ç—É–∞–ª—å–Ω—ã –¥–ª—è –ª–µ–Ω—Ç–æ—á–Ω—ã—Ö –∏ –º–æ–Ω–æ–ª–∏—Ç–Ω—ã—Ö —Ñ—É–Ω–¥–∞–º–µ–Ω—Ç–æ–≤ –æ–¥–Ω–æ- –∏ –¥–≤—É—Ö—ç—Ç–∞–∂–Ω—ã—Ö –¥–æ–º–æ–≤, –±–∞–Ω—å, —Ö–æ–∑—è–π—Å—Ç–≤–µ–Ω–Ω—ã—Ö –ø–æ—Å—Ç—Ä–æ–µ–∫, –Ω–µ–∑–∞–≤–∏—Å–∏–º–æ –æ—Ç –∏—Å–ø–æ–ª—å–∑—É–µ–º–æ–≥–æ —Å—Ç—Ä–æ–∏—Ç–µ–ª—å–Ω–æ–≥–æ –º–∞—Ç–µ—Ä–∏–∞–ª–∞."
