      title="–û–Ω–ª–∞–π–Ω –∫–∞–ª—å–∫—É–ª—è—Ç–æ—Ä —Ä–∞—Å—á–µ—Ç–∞ –≥–ª—É–±–∏–Ω—ã –ø—Ä–æ–º–µ—Ä–∑–∞–Ω–∏—è –≥—Ä—É–Ω—Ç–∞"
      text="–ë–µ—Å–ø–ª–∞—Ç–Ω—ã–π –æ–Ω–ª–∞–π–Ω –∫–∞–ª—å–∫—É–ª—è—Ç–æ—Ä —Ä–∞—Å—Å—á–∏—Ç–∞–µ—Ç –≥–ª—É–±–∏–Ω—É –ø—Ä–æ–º–µ—Ä–∑–∞–Ω–∏—è –≥—Ä—É–Ω—Ç–∞, –æ—Å–Ω–æ–≤—ã–≤–∞—è—Å—å –Ω–∞ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏, –∏–∑–ª–æ–∂–µ–Ω–Ω–æ–π –≤ —Å—Ç—Ä–æ–∏—Ç–µ–ª—å–Ω—ã—Ö –Ω–æ—Ä–º–∞—Ö –∏ –ø—Ä–∞–≤–∏–ª–∞—Ö ¬´–°—Ç—Ä–æ–∏—Ç–µ–ª—å–Ω–∞—è –∫–ª–∏–º–∞—Ç–æ–ª–æ–≥–∏—è¬ª. –¢—Ä–µ–±—É–µ—Ç—Å—è —Ç–æ–ª—å–∫–æ –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ —É–∫–∞–∑–∞—Ç—å —Ä–µ–≥–∏–æ–Ω –†–æ—Å—Å–∏–∏ –∏ –≥–æ—Ä–æ–¥, –∞ —Ç–∞–∫–∂–µ —Ç–∏–ø —Å–æ–æ—Ä—É–∂–µ–Ω–∏—è. –†–µ–∑—É–ª—å—Ç–∞—Ç—ã –≤—ã—á–∏—Å–ª–µ–Ω–∏—è –≤–∫–ª—é—á–∞—é—Ç –Ω–æ—Ä–º–∞—Ç–∏–≤–Ω—É—é –∏ —Ä–∞—Å—á–µ—Ç–Ω—É—é –≥–ª—É–±–∏–Ω—É –ø—Ä–æ–º–µ—Ä–∑–∞–Ω–∏—è –≥—Ä—É–Ω—Ç–∞ –Ω–∞ –≤—ã–±—Ä–∞–Ω–Ω–æ–π —Ç–µ—Ä—Ä–∏—Ç–æ—Ä–∏–∏. –≠—Ç–∞ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –ø–æ–º–æ–∂–µ—Ç –æ–ø—Ä–µ–¥–µ–ª–∏—Ç—å –≥–ª—É–±–∏–Ω—É –∑–∞–ª–æ–∂–µ–Ω–∏—è —Ñ—É–Ω–¥–∞–º–µ–Ω—Ç–∞."
    />
    <modal
      :modal="modal"
      @modalDisable="modalDisable"
      titlePage="–†–ê–°–ß–ï–¢ –ì–õ–£–ë–ò–ù–´ –ü–†–û–ú–ï–†–ó–ê–ù–ò–Ø –ì–†–£–ù–¢–ê"
