<script>
import { defineComponent } from "vue";
import HelpBetter from "../newComponents/HelpBetter.vue";
import BlockSection from "../newComponents/BlockSection.vue";
import BlockSectionResult from "../newComponents/BlockSectionResult.vue";
import readme from "./readme.vue";
import FormQuetions from '../newComponents/FormQuetions.vue';
import QrCode from '../newComponents/QrCode.vue';
import modal from "./modal.vue";
import axios from 'axios'

import countryData from '../../data/CountryFreezing.js'

export default defineComponent({
  data() {
    return {
      country: '',
      region: [],
      modal: false,
      city: {
        text: '–ú–∞–π–∫–æ–ø',
        value: 1
      },
      options: [],
      data: '',
      depth: '',
      freezing: '',
      building: '',
      inputValue: '',
      filteredOptions: [],
      inputCityValue: '',
      filteredCities: [],
    };
  },

  components: {
    BlockSection,
    BlockSectionResult,
    HelpBetter,
    readme,
    FormQuetions,
    QrCode,
    modal
    // "readme": readme,
    // "modal": modal,
    // HelpBetter
  },
  methods: {
    showAllOptions() {
      this.filteredOptions = this.country.region;
    },
    filterOptions() {
      if (!this.inputValue) {
        this.showAllOptions();
        return;
      }
      this.filteredOptions = this.country.region.filter(option =>
        option.text.toLowerCase().includes(this.inputValue.toLowerCase())
      );
    },
    selectOption(option) {
      this.inputValue = option.text;
      this.region = option;
      this.filteredOptions = [];
      this.city = ''
      this.inputCityValue = ''
    },
    updateRegion() {
      setTimeout(() => {
        this.filteredOptions = [];
      }, 150);
    },

    showAllCities() {
      this.filteredCities = this.region.city;
    },
    filterCities() {
      if (!this.inputCityValue) {
        this.showAllCities();
        return;
      }
      this.filteredCities = this.region.city.filter(city =>
        city.text.toLowerCase().includes(this.inputCityValue.toLowerCase())
      );
    },
    selectCity(city) {
      this.inputCityValue = city.text;
      this.city = city;
      this.filteredCities = [];
      this.result();
    },
    updateCity() {
      setTimeout(() => {
        this.filteredCities = [];
      }, 150);
    },

    result() {
      let buildingData = this.building.value
      let regionData = this.region.value
      let cityData = this.city.value

      this.data[buildingData].filter((item) => {
        if (item.region === regionData && item.city === cityData) {
          this.depth = item.depth
          this.freezing = item.freezing
        }
      })
    },
    modalDisable() {
      this.modal = false
    },
    modalActive() {
      this.modal = true
    },
    saveFile() {
      let state = {
        head1: `–†–∞—Å—á–µ—Ç–Ω–∞—è –≥–ª—É–±–∏–Ω–∞ –ø—Ä–æ–º–µ—Ä–∑–∞–Ω–∏—è –¥–ª—è –≥–æ—Ä–æ–¥–∞ ${this.city.text} –ì–ª–∏–Ω–∞ –∏ —Å—É–≥–ª–∏–Ω–æ–∫ 0‚Å∞C: `,
        head2: `–†–∞—Å—á–µ—Ç–Ω–∞—è –≥–ª—É–±–∏–Ω–∞ –ø—Ä–æ–º–µ—Ä–∑–∞–Ω–∏—è –¥–ª—è –≥–æ—Ä–æ–¥–∞ ${this.city.text} –°—É–ø–µ—Å—å, –ø–µ—Å–æ–∫ –ø—ã–ª–µ–≤–∞—Ç—ã–π –∏ –º–µ–ª–∫–∏–π 0‚Å∞C: `,
        head3: `–†–∞—Å—á–µ—Ç–Ω–∞—è –≥–ª—É–±–∏–Ω–∞ –ø—Ä–æ–º–µ—Ä–∑–∞–Ω–∏—è –¥–ª—è –≥–æ—Ä–æ–¥–∞ ${this.city.text} –ü–µ—Å–æ–∫ —Å—Ä–µ–¥–Ω–µ–π –∫—Ä—É–ø–Ω–æ—Å—Ç–∏, –∫—Ä—É–ø–Ω–æ–π –∏–ª–∏ –≥—Ä–∞–≤–µ–ª–∏—Å—Ç—ã–π 0‚Å∞C: `,
